import { SnackbarContextNext } from 'notistack/build/SnackbarContext'
import { useCallback, useContext } from 'react'

enum EVariant {
  default = 'default',
  error = 'error',
  success = 'success',
  warning = 'warning',
  info = 'info',
}

interface IEnqueueOptions {
  /** Type of the snackbar */
  variant: EVariant
  /** Event fired when user clicks on action button (if any) */
  onClickAction(): void
  /**
   * You can pass material-ui Snackbar props here, and they will be applied to this individual snackbar.
   * for example, this particular snackbar will be dismissed after 1sec.
   */
  autoHideDuration: number
  action: ReactNode[]
}

interface IEnqueueSnackbar {
  (message: string, options?: Partial<IEnqueueOptions>)
}

const successVariant = { variant: EVariant.success }
const errorVariant = { variant: EVariant.error }
const warningVariant = { variant: EVariant.warning }

export function useSnackBar() {
  const enqueueSnackbar: IEnqueueSnackbar = useContext(SnackbarContextNext)
  const enqueueSuccess = useCallback(
    (errorMessage: string) => enqueueSnackbar(errorMessage, successVariant),
    [],
  )
  const enqueueError = useCallback(
    (errorMessage: string) => enqueueSnackbar(errorMessage, errorVariant),
    [],
  )
  const enqueueWarning = useCallback(
    (errorMessage: string) => enqueueSnackbar(errorMessage, warningVariant),
    [],
  )
  return { enqueueSnackbar, enqueueSuccess, enqueueError, enqueueWarning }
}
