import React from 'react'

import { GridLayout } from '../../../atoms/GridLayout'
import { LoginPageBackground } from '../../../atoms/LoginPageBackground'
import { PageTitle } from '../../../atoms/PageTitle'
import { PasswordChangeForm } from '../organisms/PasswordChangeForm'

export interface IPasswordChangeProps {
  onPasswordChange(password: string): void
  pageTitle: string
  buttonLabel: string
}

function getLayout() {
  return [
    { i: 'title', x: 6, y: 2, w: 8, h: 1 },
    { i: 'form', x: 6, y: 3, w: 8, h: 4 },
  ]
}

export const PasswordChangePage: React.SFC<IPasswordChangeProps> = ({
  onPasswordChange,
  pageTitle,
  buttonLabel,
}) => (
  <LoginPageBackground>
    <PageTitle title={pageTitle} />
    <GridLayout layouts={getLayout()}>
      <div key="title">
        <PageTitle.Display />
      </div>
      <div key="form">
        <PasswordChangeForm
          onSubmitChange={onPasswordChange}
          buttonLabel={buttonLabel}
        />
      </div>
    </GridLayout>
  </LoginPageBackground>
)
