import React from 'react'
import { Field, FieldType, ValidationResult } from 'react-form'
import styled, { css } from 'styled-components'
import { isEmpty } from 'validator'

import { i18n } from '../i18n'
import { Spacing } from '../styling'

interface IProps extends React.InputHTMLAttributes<never> {
  field: FieldType
  defaultValue?: string
  inputWidth?: number
  inputWidthUnit?: string
  isRequired?: boolean
  validatioName?: string
  validate?(value: string): ValidationResult
}

export type TFormInputTextBoxProps = IProps

const inputStyle = css`
  outline: none;
  margin: ${Spacing.MarginAroundFormComponents};
`

const invalidInputStyle = css`
  border: '1px red solid';
`

export const FormInputStyled = styled.input`
  ${inputStyle};
  ${({ invalid }: { invalid?: boolean }) => invalid && invalidInputStyle};
`

export const FormInputTextBox = React.forwardRef<HTMLInputElement, IProps>(
  (
    {
      field,
      inputWidth = Spacing.DefaultInputWidth,
      inputWidthUnit = Spacing.DefaultSizeUnit,
      isRequired,
      validatioName,
      validate,
      ...props
    },
    inputRef,
  ) => (
    <Field
      field={field}
      validate={
        validate ||
        (value => {
          if (isRequired && isEmpty(value + '')) {
            return i18n.t`${validatioName} is required`
          }
          return null
        })
      }
    >
      {fieldApi => (
        <FormInputStyled
          width={`${inputWidth}${inputWidthUnit}`}
          value={!fieldApi.value && fieldApi.value !== 0 ? '' : fieldApi.value}
          onChange={ev => {
            fieldApi.setTouched(true)
            fieldApi.setValue(ev.target.value)
          }}
          // @ts-ignore
          ref={inputRef}
          {...props}
        />
      )}
    </Field>
  ),
)
