import { Trans } from '@lingui/react'
import React from 'react'
import styled from 'styled-components'

import { FormInputStyled } from '../../../atoms/FormInputTextBox'
import { PasswordInput } from '../../../atoms/PasswordInput'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {
  onSubmitChange(password: string): void
  buttonLabel: string
}

const PasswordContainerStyled = styled.div`
  display: inline-block;
  margin: 0.5rem;
`
const SubmitButtonContainerStyled = styled.div`
  display: inline-block;
  width: 14rem;
  margin: 0.5rem;
`
const ShowPasswordButton = styled(TouchButton)`
  display: inline-block;
  top: 0.5rem;
`

const ErrorContainer = styled.div`
  background-color: ${Colors.red};
  opacity: 0.5;
  padding: 1rem;
  width: 31rem;
`

export const PasswordChangeForm: React.SFC<IProps> = ({
  onSubmitChange,
  buttonLabel,
}) => (
  <PasswordInput>
    {({
      getInputProps,
      valid,
      errors,
      touched,
      isVisible,
      toggleShowPassword,
      getPassword,
    }) => (
      <>
        <PasswordContainerStyled>
          <FormInputStyled
            {...getInputProps()}
            name="password"
            placeholder={i18n.t`new password`}
            invalid={touched && !valid}
          />
        </PasswordContainerStyled>

        <ShowPasswordButton
          color={Colors.greyMiddle}
          onClick={toggleShowPassword}
          iconName={isVisible ? 'blur_off' : 'blur_on'}
          iconSize={2}
        />

        <SubmitButtonContainerStyled>
          <TouchButton
            color={Colors.green}
            disabled={!valid}
            onClick={e => {
              e.preventDefault()
              onSubmitChange(getPassword())
            }}
          >
            {buttonLabel}
          </TouchButton>
        </SubmitButtonContainerStyled>

        {touched &&
          !valid && (
            <ErrorContainer>
              <Trans>Password is invalid due to following errors:</Trans>
              <ul>
                {errors.map(e => (
                  <li key={e.key}>{e.message}</li>
                ))}
              </ul>
            </ErrorContainer>
          )}
      </>
    )}
  </PasswordInput>
)
