import React, { HTMLAttributes, useCallback, useState } from 'react'
import PasswordInputComponent from 'react-password-indicator'

import { i18n } from '../i18n'

interface IProps {
  children(props: IRenderProps): ReactNode
}

interface IRenderProps {
  getInputProps(): Partial<HTMLAttributes<HTMLInputElement>>
  valid: boolean
  errors: ReadonlyArray<{ key: string; message: string }>
  touched: boolean
  isVisible: boolean
  toggleShowPassword(): void
  getPassword(): string
}

export const PasswordInput: React.SFC<IProps> = ({ children }) => {
  const [password, setPassword] = useState('')
  const messages = {
    minLen: length => i18n.t`Minimal length is ${length}`,
    maxLen: length => i18n.t`Maximal length is ${length}`,
    digits: i18n.t`Requires at least one digit`,
    uppercaseChars: i18n.t`Requires at least one uppercase character`,
  }
  const getPassword = useCallback(() => password, [password])
  return (
    <PasswordInputComponent
      minLen={6}
      maxLen={30}
      digits={1}
      uppercaseChars={1}
      defaultMessages={messages}
      onChange={setPassword}
    >
      {props =>
        children({
          ...props,
          getPassword,
        })
      }
    </PasswordInputComponent>
  )
}
